  //     this.newAr.push(this.products[a]);
  //       for(let bds of this.newAr){
  //           for(let c in bds ){
  //             this.test.push(bds[c]);
  //         // console.log(bds[c])
  //           }      
  //       }
  // }
  //   let val = ev.target.value;
